html {
  font-size: 10px;
  line-height: 10px;
  font-weight: 400; }

body {
  font-size: 1.6rem;
  line-height: 2rem;
  font-weight: 400;
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 320px;
  min-height: 100vh;
  margin: 0 auto;
  font-family: "Roboto", "Arial", sans-serif;
  background-color: #ffffff;
  color: #383736;
  overflow-x: hidden;
  background-color: #e5e5e5;
  display: flex;
  justify-content: center;
  align-items: center; }

body,
*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

@font-face {
  font-family: "Roboto";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: local("Roboto"), local("Roboto-Regular"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-display: swap;
  font-weight: 500;
  font-style: normal;
  src: local("Roboto"), local("Roboto-Medium"), url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: local("Roboto"), local("Roboto-Bold"), url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.container {
  width: 1200px;
  padding: 0 40px;
  margin: 0 auto;
  box-sizing: content-box; }
  @media (max-width: 1279px) {
    .container {
      min-width: 864px;
      padding: 0 28px; } }
  @media (max-width: 919px) {
    .container {
      min-width: 712px; } }
  @media (max-width: 767px) {
    .container {
      min-width: 304px;
      padding: 0 8px; } }

.flat {
  padding: 24px 32px 24px 24px;
  background-color: #ffffff;
  border-radius: 8px;
  position: relative;
  min-height: 226px;
  display: grid;
  grid-template-columns: -webkit-max-content 1fr 268px;
  grid-template-columns: max-content 1fr 268px;
  grid-column-gap: 32px; }

.flat__delete {
  font-size: 1.4rem;
  line-height: 1.6rem;
  font-weight: 500;
  padding: 0;
  border: none;
  font-family: "Roboto", "Arial", sans-serif;
  color: inherit;
  background-color: inherit;
  border-radius: 4px;
  cursor: pointer;
  display: block;
  position: absolute;
  top: 16px;
  right: 14px;
  width: 28px;
  height: 28px;
  padding: 0;
  border: none;
  border-radius: 4px;
  background-color: transparent; }
  .flat__delete svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    fill: #c4c4c4; }
  .flat__delete:hover {
    background-color: #f5f4f2;
    transition: 0.3s ease; }
    .flat__delete:hover svg {
      fill: #383736; }
  .flat__delete:active {
    background-color: #e7e4de;
    transition: 0.3s ease; }

.flat__img {
  display: flex;
  justify-content: center;
  align-items: center; }

.flat__description {
  border-right: 1px solid #f0eeeb;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.flat__description h2 {
  margin: 0;
  padding: 0;
  font-size: 2.4rem;
  line-height: 3.6rem;
  font-weight: 700; }

.flat__params {
  list-style: none;
  margin: 0;
  padding: 0;
  margin: 9px 0 12px;
  display: grid;
  grid-template-columns: repeat(3, 120px);
  grid-column-gap: 24px;
  grid-row-gap: 12px; }

.flat__params h3 {
  margin: 0;
  padding: 0;
  font-size: 1.2rem;
  line-height: 1.6rem;
  font-weight: 400;
  color: #989691; }

.flat__params p {
  margin: 0;
  padding: 0;
  font-size: 1.4rem;
  line-height: 2rem;
  font-weight: 500; }

.flat__tags p {
  margin: 0;
  padding: 0; }

.flat__price {
  font-size: 1.4rem;
  line-height: 2rem;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #989691; }

.flat__price p:first-of-type {
  margin: 0;
  padding: 0;
  margin-bottom: 1px; }

.flat__price p:last-of-type {
  margin: 0;
  padding: 0;
  max-width: 200px; }

.flat__hot-price {
  font-size: 2.4rem;
  line-height: 3.6rem;
  font-weight: 700;
  margin: 0;
  padding: 0;
  margin-bottom: 14px;
  color: #383736; }

.flat__price time {
  margin: 0;
  padding: 0;
  color: #2794db; }

.flat__developer-price {
  color: #eb4b51; }

.flat__currency {
  color: inherit; }

.flat__button {
  margin-top: auto; }

@media (max-width: 1279px) {
  .flat__img {
    align-items: flex-start; }
  .flat__params {
    grid-template-columns: repeat(2, 125px);
    grid-column-gap: 20px;
    grid-row-gap: 12px;
    margin-bottom: 18px; }
  .flat__params li:nth-of-type(3) {
    grid-column-start: 2;
    grid-row-start: 2; }
  .flat__description {
    padding-right: 24px; } }

@media (max-width: 919px) {
  .flat {
    padding: 24px 29px 24px 24px;
    display: grid;
    grid-template-columns: -webkit-max-content 1fr;
    grid-template-columns: max-content 1fr;
    grid-column-gap: 32px;
    grid-row-gap: 23px; }
  .flat__delete {
    top: 14px; }
  .flat__description {
    border: none;
    padding-right: 0; }
  .flat__price {
    padding-top: 24px;
    padding-left: 8px;
    border-top: 1px solid #f0eeeb;
    grid-column-start: 1;
    grid-column-end: 3; }
  .flat__button {
    margin-top: 24px; } }

@media (max-width: 767px) {
  .flat {
    padding: 16px 20px 20px;
    grid-template-columns: 1fr;
    grid-row-gap: 16px; }
  .flat__description {
    margin-bottom: 4px; }
  .flat__description h2 {
    font-size: 2rem;
    line-height: 2.8rem;
    font-weight: 700; }
  .flat__params {
    grid-template-columns: repeat(2, calc(50% - 10px));
    margin: 15px 0 16px; }
  .flat__params h3 {
    margin-bottom: 1px; }
  .flat__price {
    padding: 20px 0 0;
    border-top: 1px solid #f0eeeb;
    grid-column-start: 1;
    grid-column-end: 2; }
  .flat__hot-price {
    font-size: 2rem;
    line-height: 2.8rem;
    font-weight: 700;
    margin-top: -2px; }
  .flat__button {
    margin-top: 16px; } }

.tags {
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 1.2rem;
  line-height: 1.6rem;
  font-weight: 500;
  display: flex;
  flex-wrap: wrap;
  gap: 16px; }

.tags__item {
  display: flex;
  align-items: baseline;
  gap: 10px;
  padding: 8px 16px 8px 14px;
  border-radius: 22px; }
  .tags__item--green {
    background-color: rgba(100, 184, 81, 0.1);
    color: #64b851; }
  .tags__item--red {
    background-color: rgba(235, 75, 81, 0.1);
    color: #eb4b51; }

@media (max-width: 1279px) {
  .tags {
    gap: 8px; } }

@media (max-width: 919px) {
  .tags {
    justify-content: flex-start;
    gap: 16px; } }

@media (max-width: 767px) {
  .tags {
    gap: 8px; } }

.button {
  font-size: 1.4rem;
  line-height: 1.6rem;
  font-weight: 500;
  padding: 0;
  border: none;
  font-family: "Roboto", "Arial", sans-serif;
  color: inherit;
  background-color: inherit;
  border-radius: 4px;
  cursor: pointer;
  display: block;
  background-color: #fac62d;
  color: #383736;
  padding: 14px 24px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 6px; }
  .button--large {
    padding: 14px 24px; }
  .button--medium {
    padding: 8px 16px; }
  .button--small {
    padding: 4px 12px; }
  .button:hover {
    background-color: #f0b505;
    transition: background-color 0.3s ease; }
  .button:active {
    background-color: #e1ab08;
    transition: background-color 0.1s ease; }
